a {
  text-decoration: none;
}
.cantClick {
  cursor: default;
  user-select: none;
  pointer-events: none;
}
.bg_img {
  opacity: 00%;
  position: absolute;
  top: 0;
  left: -120px;
  display: none;
  width: 105%;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Pretendard-Regular";
}

ul {
  list-style: none;
}
@font-face {
  font-family: "Pretendard-Regular";
  src: url("https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
  /* 기본 폰트 가독성 프리텐다드 */
}
@font-face {
  font-family: "SBAggroB";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SBAggroB.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
  /* 두꺼운 타이틀전용 */
}

@font-face {
  font-family: "Cafe24Ssurround";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24Ssurround.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
  /* 둥글둥글글씨체 */
}
.root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.App {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.349);
}
.view {
  display: flex;
  width: 100%;
  height: 100%;
}
/* ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡheaderㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ */
header {
  width: 300px;
  height: 100vh;
  min-height: 700px;
  background-color: rgba(0, 0, 0, 0.384);
  opacity: 100%;
  /* border-right:2px solid yellow; */
  position: relative;
}
header.on {
  transform: translateX(-100%);
  transition: 0.5s;
}

.bg_header {
  position: absolute;
  width: 101%;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 100%;
  pointer-events: none;
  user-select: none;
  filter: blur(4px);
}

/* logo */
.logo {
  width: 50%;
  height: 60px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.logoWrap {
  display: flex;
  align-items: center;
  position: relative;
  width: 50%;
}
.logoWrap img {
  width: 80%;
  position: absolute;
  right: 0;
  filter: invert(1);
  pointer-events: none;
}
.stream {
  font-size: 16px;
  line-height: 20px;
  font-family: "SBAggroB";
  color: white;
  cursor: pointer;
  user-select: none;
}

/* profile */
.profile {
  width: 85%;
  height: 90px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.295);
  margin: 0px auto;
  /* padding-left:8px; */
  display: flex;
  padding-top: 8px;
  gap: 18px;
}
.profile .circle {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgba(0, 250, 154, 0.521);
  overflow: hidden;
}
.profile .circle img {
  width: 90%;
}
.userName {
  font-size: 18px;
  color: white;
  line-height: 70px;
}
.userName span {
  font-size: 10px;
  transform: translateY(-3px);
  display: inline-block;
  margin-left: 2px;
}

/* 콘박스 */
.con_box {
  width: 85%;
  height: 210px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.295);
  margin: 0px auto;
  margin-top: 20px;
}
.con_box > p {
  /* padding-left:8px; */
  font-size: 14px;
  color: white;
  margin-bottom: 10px;
}

/* 카테고리 */

.category {
  width: 85%;
  height: 250px;
  margin: 0px auto;
  margin-top: 20px;
}
.category > p {
  /* padding-left:8px; */
  font-size: 14px;
  color: white;
  margin-bottom: 10px;
}

/* about */

.about {
  width: 85%;
  height: 200px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 15px;
}
@media (max-height: 850px) {
  .about > p {
    display: none;
  }
  .about .linkWrap {
    display: none;
  }
  .about {
    height: 70px;
  }
}
.about > p {
  padding-left: 8px;
  font-size: 14px;
  color: white;
  margin-bottom: 10px;
}
.copyright {
  color: #bebebe;
  font-size: 9.5px;
  border-top: 2px solid rgba(255, 255, 255, 0.295);
  padding-top: 20px;
  margin-top: 20px;
  text-align: center;
}
.copyright span {
  margin-left: 5px;
}

/* link ul */
.linkWrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 13px;
}
.linkWrap li {
  display: flex;
  width: 100%;
  height: 30px;
  padding-left: 8px;
  gap: 10px;
  align-items: center;
}
.linkWrap li .linkMenu {
  display: flex;
  height: 30px;
  gap: 10px;
  align-items: center;
}
.linkWrap li .icon {
  width: 30px;
  height: 25px;
  background-color: rgba(128, 128, 128, 0.534);
}
.linkWrap li h3 {
  display: flex;
  align-items: center;
  color: rgb(175, 175, 175);
  font-weight: normal;
  font-size: 15px;
}
.linkWrap li:hover {
  cursor: pointer;
}
.linkWrap li:hover h3 {
  font-weight: bold;
  cursor: pointer;
  color: #fff;
  filter: blur(0.6px);
}
.linkWrap li h3.on {
  font-weight: bold;
  color: white;
}

/* ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ콘텐츠ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ */
.content {
  width: calc(100% - 300px);
  height: 100%;
  /* overflow-y:scroll; */
  background: linear-gradient(to bottom, #6d379c91, #0000005e);
  /* background-color:blue; */

  opacity: 99.9%;
}

/* ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ콘텐츠메인ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ  */
.contentWrap {
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: 0px auto;
  background-color: #000000c5;
}
/* 스크롤바숨기기 */
.contentWrap::-webkit-scrollbar {
  width: 0px;
  display: none;
}
/* 비쥬얼 */
.contentWrap .visual {
  width: 95%;
  height: 700px;
  background: url(../public/io.jpg) no-repeat fixed;

  background-size: 85%;
  background-position: right -170px;
  margin: 0px auto;
  margin-top: 30px;
  border-top-left-radius: 70px;
  border-top-right-radius: 70px;
  position: relative;
  margin-bottom: 30px;
}

/* 콘텐츠서브 */
.contentWrap > h2 {
  font-size: 30px;
  color: white;
  font-weight: 900;
  /* font-family: "SBAggroB"; */
  text-align: left;
  /* border: 1px solid white; */
  line-height: 80px;
  padding-left: 30px;
  /* font-family: "Pretendard-Regular"; */
}

.contentWrap .visual.popular {
  width: 100%;
  height: 500px;
  background: url(../public/renderman.jpg) no-repeat fixed;

  background-size: 100%;
  background-position: right 30px;
  margin: 0px auto;
  margin-top: 0px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  position: relative;
  margin-bottom: 0px;

  /* border: 5px solid white; */
}
.contentWrap .visual.onNow {
  width: 100%;
  height: 500px;
  background: url(../public/10_up.jpg) no-repeat fixed;

  background-size: 90%;
  background-position: right -200px;
  margin: 0px auto;
  margin-top: 0px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  position: relative;
  margin-bottom: 0px;
}

.contentWrap .visual.upNow {
  width: 100%;
  height: 500px;
  background: url(../public/character_head.jpg) no-repeat fixed;

  background-size: 90%;
  background-position: right -200px;
  margin: 0px auto;
  margin-top: 0px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  position: relative;
  margin-bottom: 0px;
}

/*  */
.visual h2 {
  width: 100%;
  position: relative;
  height: 100%;
}
/* 콘텐츠서브 */
.visual .visual_title {
  width: auto;
  height: auto;
  font-size: 60px;
  position: absolute !important;
  color: white;
  top: 50%;
  left: 50%;
  display: block;
  font-family: "Cafe24Ssurround";
  z-index: 10;
  transform: translate(-50%, -50%);
  text-shadow: 3px 3px 5px #0000005e;
}
/*  */
.main_visual_img {
  position: absolute;
  width: 40%;
  left: 50%;
  top: 42%;
  transform: translate(-50%, -50%);
  opacity: 100%;
}
.visual p {
  color: rgb(255, 255, 255);
  position: absolute;
  bottom: 50px;
  left: 50%;
  font-size: 30px;
  transform: translateX(-50%);
  font-family: "Cafe24Ssurround";
  opacity: 100%;
  text-shadow: 0px 3px 5px rgba(0, 0, 0, 0.664);
}
/* 무비들 */
.movie {
  width: 100%;
  height: 200vh;
}
.subMovie {
  padding-top: 50px;
}

/* 무비 상자 */
.line_wrap {
  width: 97%;

  height: 422px;

  margin: 0px auto;
  position: relative;
}
.title_line {
  font-size: 24px;
  margin-left: 30px;
  font-weight: bold;
  color: white;
}
.picture_line {
  display: flex;
  /* gap: 33px; */
  margin-left: 15px;
  flex-wrap: nowrap;
  margin-top: 10px;
  margin-bottom: 50px;
  /* 오류날시 sticy제거 */
  position: sticky;

  z-index: 1000;
  /* ----------- */
}
.picture_box {
  /* width: 258px !important; */
  /* height: 348px; */
  width: 255.4px !important;
  height: 334.5px;
  background-color: transparent;

  flex-shrink: 0;
  border-radius: 15px;
  overflow: hidden;
  background-size: 100%;
  transition: scale 0.1s; /* transform 속성에 대한 트랜지션 추가 */
  display: flex;
  border: 3px solid transparent;
  align-items: center;
  box-sizing: content-box;
}
.picture_box .imgWrap {
  width: 90%;
  height: 92.5%;
  transition: 0.2s;
  margin: 0px auto;
  display: flex;
}
.picture_box img {
  border-radius: 11px;
  overflow: hidden;
  display: block;
  margin: 2px auto 2px;
}
.picture_box.on:hover {
  border: 3px solid white;
}
.picture_box.on:hover .imgWrap {
  scale: calc(1.07);
}

/* 노가다 드가자 */
.picture_line.box0 .picture_box {
  opacity: 100%;
  transition: 0.5s;
}
.picture_line.box1 .picture_box {
  opacity: 100%;
  transition: 0.5s;
}
.picture_line.box2 .picture_box {
  opacity: 100%;
  transition: 0.5s;
}
.picture_line.box3 .picture_box {
  opacity: 100%;
  transition: 0.5s;
}
