/* 무비안 버튼 */
.nav_wrap {
  width: 100%;
  position: absolute;
  /* border: 1px solid white; */

  height: 100%;
}
/* 오른쪽버튼 */
.nav_wrap .right {
  position: absolute;
  display: block;
  top: 50%;
  right: 0px;
  z-index: 1000000;
  filter: invert(1);
  cursor: pointer;
  opacity: 80%;
}
.nav_wrap .right:hover {
  opacity: 100%;
  scale: calc(1.2);
}
.nav_wrap .right img {
  width: 40px;
}
/* 왼쪽버튼 */
.nav_wrap .left {
  position: absolute;
  display: block;
  top: 50%;
  left: -20px;
  z-index: 1000000;
  filter: invert(1);
  cursor: pointer;
  opacity: 80%;
}
.nav_wrap .left:hover {
  opacity: 100%;
  scale: calc(1.2);
}
.nav_wrap .left img {
  width: 40px;
}

